<template>
  <div class="page" ref="page">
    <div class="lookcartoon" ref="imglist">
      <OneAChapter
        v-for="item in seelist"
        :key="item.chapter_id"
        :onect="item"
        :val="LDvalue"
      />
      <div v-if="isnoimg" class="no-img">已经是最后一章了......</div>
    </div>

    <transition
      name="one"
      enter-active-class="animate__animated animate__slideInDown"
      leave-active-class="animate__animated animate__slideOutUp"
    >
      <van-nav-bar
        v-if="ismenu"
        @click-left="bruk"
        :fixed="true"
        :z-index="99"
        :border="false"
      >
        <template #left>
          <van-icon name="revoke" color="white" size="22" />
        </template>

        <template #title>
          <p>{{ lookin != null ? cartoonlist[lookin].chapter_name : null }}</p>
          <span>{{ value }}/{{ max }}</span>
        </template>

        <template #right>
          <van-popover
            v-model="showPopover"
            trigger="click"
            :actions="actions"
            placement="bottom-end"
            @select="onSelect"
          >
            <template #reference>
              <van-icon name="ellipsis" color="white" size="28" />
            </template>
          </van-popover>
        </template>
      </van-nav-bar>
    </transition>

    <transition
      name="two"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div v-if="ismenu" class="menu-bottom">
        <div v-if="!isStart" class="ttop">
          <van-icon
            @click.stop="previousPage"
            name="arrow-left"
            size="19"
            color="#e1e1e1"
          />

          <van-slider
            v-model="value"
            :min="1"
            :max="max"
            :step="1"
            active-color="#f36556"
            inactive-color="rgba(255,255,255,0.3)"
            @input="ondrag"
          >
            <template #button>
              <div class="custom-button"></div>
            </template>
          </van-slider>

          <p>{{ value }}/{{ max }}</p>

          <van-icon @click="nextPage" name="arrow" size="19" color="#e1e1e1" />
        </div>
        <div v-else class="ttop sliderbox">
          <van-slider
            v-model="speed"
            :min="1"
            :max="50"
            :step="1"
            active-color="#f36556"
            inactive-color="rgba(255,255,255,0.3)"
            @input="onspeed"
          >
            <template #button>
              <div class="custom-button">{{ speed }}</div>
            </template>
          </van-slider>
        </div>

        <!-- 标签页栏 -->
        <van-tabbar :fixed="false" :border="false" inactive-color="white">
          <van-tabbar-item @click="onsetting" icon="setting-o"
            >设置</van-tabbar-item
          >
          <van-tabbar-item
            :icon="isStart ? 'pause-circle-o' : 'play-circle-o'"
            @click="onautoplay"
            >{{ isStart ? "暂停" : "自动" }}</van-tabbar-item
          >
          <van-tabbar-item @click="onhuazhi" icon="browsing-history-o"
            >画质</van-tabbar-item
          >
          <van-tabbar-item @click="onliangdu" icon="bulb-o"
            >亮度</van-tabbar-item
          >
          <van-tabbar-item @click="onmulu" icon="bars">目录</van-tabbar-item>
        </van-tabbar>
      </div>
    </transition>

    <!-- 设置弹出层 -->
    <van-action-sheet v-model="showbtm" :round="false">
      <div class="content">
        <div>左手翻页</div>
        <div>右手翻页</div>
        <div class="actsetting">竖屏卷轴</div>
        <div>横屏卷轴</div>
      </div>
    </van-action-sheet>

    <!-- 画质弹出层 -->
    <van-popup v-model="showHuazhi">
      <div class="huazhi">
        <ul>
          <li
            @click="$store.commit('setactHz', 0)"
            :class="{ 'activ-hz': actHz == 0 }"
          >
            标清
          </li>
          <li
            @click="$store.commit('setactHz', 1)"
            :class="{ 'activ-hz': actHz == 1 }"
          >
            高清
          </li>
          <li
            @click="$store.commit('setactHz', 2)"
            :class="{ 'activ-hz': actHz == 2 }"
          >
            超清
          </li>
        </ul>
      </div>
    </van-popup>

    <!-- 亮度弹出层 -->
    <van-popup v-model="showLiangdu">
      <div class="liangdu">
        <van-slider
          v-model="LDvalue"
          :min="1"
          :max="10"
          :step="1"
          bar-height="2px"
          active-color="#ee0a24"
          inactive-color="rgba(255,255,255,0.6)"
          @input="onstartTD"
        />
        <ul class="btnser">
          <li @click="onbtnser(1, 10)" :class="{ onact: LDact == 1 }">
            <van-icon size="19" name="passed" />默认亮度
          </li>
          <li @click="onbtnser(2, 4)" :class="{ onact: LDact == 2 }">
            <van-icon size="19" name="passed" />夜间模式
          </li>
        </ul>
      </div>
    </van-popup>

    <!-- 右侧目录弹出层 -->
    <van-popup v-model="showrig" position="right" :style="{ height: '100%' }">
      <div v-if="!showbookmark" class="bookmark-catalog_top">
        <div class="t-left" @click="getBookData">
          <van-icon name="apps-o" size="20" />
          <span>全集</span>
        </div>
        <div class="t-rigth" @click="iszf = !iszf">
          <van-icon v-if="iszf" name="descending" size="22" />
          <van-icon v-else name="ascending" size="22" />
          <span>{{ iszf ? "正序" : "倒序" }}</span>
        </div>
      </div>
      <div v-else class="bookmark-catalog_top">
        <div class="t-left"></div>
        <div class="t-rigth" @click="show = true">
          <van-icon name="delete-o" size="22" />
          <span>清空</span>
        </div>
      </div>

      <!-- 中间目录容器 -->
      <div class="bookmark-catalog_center">
        <ul v-show="!showbookmark" ref="mululist">
          <bookmarkCatalog
            v-for="(item, index) in iszf ? cartoonlist : fancartoonlist"
            :key="item.chapter_id"
            :itemdata="item"
            :lookinbook="cartoonlist[lookin]"
            @update:bookcatalog="onbc(item, index)"
          />
        </ul>
        <ul v-show="showbookmark">
          <bookmarkCatalog
            v-for="item in bookitem.bookmark"
            :key="item.chapter_id"
            :itemdata="item"
            :isshuqian="true"
            @update:bookcatalog="onshuqian(item)"
          />
        </ul>
      </div>

      <div class="bookmark-catalog_bottom">
        <div v-if="!showbookmark" class="t-left" @click="addbookmark">
          <van-icon name="shop-collect-o" size="22" />
          <span>添加</span>
        </div>
        <div v-else class="t-left" @click="showbookmark = false">
          <van-icon name="bars" size="22" />
          <span>目录</span>
        </div>

        <div class="t-rigth" @click="showbookmark = true">
          <van-icon name="shop-o" size="22" />
          <span>书签</span>
        </div>
      </div>
    </van-popup>
    <!-- <div class="test"></div> -->

    <van-dialog
      v-model="show"
      overlayClass="deletemark"
      :closeOnClickOverlay="true"
      show-cancel-button
      :width="280"
      @confirm="deleteAll"
    >
      <img width="100%" src="https://img.yzcdn.cn/vant/apple-3.jpg" />
    </van-dialog>

    <!-- 帮助弹出层 -->
    <div @click="showHelomark = false" v-if="showHelomark" class="helo-mark">
      <span :style="{ backgroundImage: 'url(' + heloimg + ')' }"></span>
    </div>
  </div>
</template>

<script>
import OneAChapter from "@/components/OneAChapter.vue";
import bookmarkCatalog from "@/components/bookmarkCatalog.vue";
import heloimg from "@/assets/2.png";
import { mapState } from "vuex";

export default {
  data() {
    return {
      lookin: null,
      seeindex: null,
      seelist: [],
      seelistHei: [],
      // 看到最后一章的提示
      isnoimg: false,
      showPopover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [
        { text: "首页", icon: "wap-home-o" },
        { text: "分享", icon: "share-o" },
        { text: "反馈", icon: "edit" },
        { text: "帮助", icon: "browsing-history-o" },
      ],
      value: 1,
      LDvalue: 10,
      LDact: 0,
      // showbtm showrig 控制点击底部菜单指定按钮的弹出层出现
      showbtm: false,
      showrig: false,
      showHuazhi: false,
      showLiangdu: false,
      // 触碰屏幕的信息
      touchstartX: null,
      touchstartY: null,
      touchstarttime: null,
      ismenu: false,
      // 自动播放信息
      isStart: false,
      autotimer: null,
      speed: 25,
      // 目录使用正序，倒序？
      iszf: true,
      // 书签
      showbookmark: false,
      show: false,
      // 当前具体的图片
      lookindex: 0,
      allhei: [[], [], []],
      heloimg,
      showHelomark: false,
    };
  },
  computed: {
    max() {
      return this.lookin != null ? this.cartoonlist[this.lookin].end_var : 2;
    },
    isnotBookmark() {
      return Boolean(this.bookitem.bookmark.length);
    },
    seeitemheight() {
      return this.jsImg();
    },
    ...mapState({
      actHz: "actHz",
      bookitem: "bookitem",
      cartoonlist: (state) => state.bookitem.chapter_list,
      fancartoonlist: (state) => {
        let arr = [];
        state.bookitem.chapter_list.forEach((item) => {
          arr.unshift(item);
        });

        return arr;
      },
    }),
  },
  watch: {
    lookin(xin) {
      if (xin != null) {
        this.$store.commit("setBookitemLookin", this.lookin);
        this.$store.commit("HcCartoon");
      }
    },
  },
  methods: {
    // 计算点击了哪个位置
    jsScreen(x, y) {
      let thisX = this.$refs.page.offsetWidth;
      let thisY = this.$refs.page.offsetHeight;

      let clickRegionW = thisX * 0.6;
      let clickRegionH = thisY * 0.7;

      let clickRegionLTx = thisX * 0.2;
      let clickRegionLTy = thisY * 0.15;
      let clickRegionRBx = clickRegionLTx + clickRegionW;
      let clickRegionRBy = clickRegionLTy + clickRegionH;

      if (x < clickRegionLTx && y > clickRegionLTy && y < clickRegionRBy) {
        console.log("点击了左侧");
      } else if (
        x > clickRegionRBx &&
        y > clickRegionLTy &&
        y < clickRegionRBy
      ) {
        console.log("点击了右侧");
      } else if (y < clickRegionLTy) {
        console.log("点击了顶部");
      } else if (y > clickRegionRBy) {
        console.log("点击了底部");
      } else {
        this.ismenu = !this.ismenu;
        console.log("点击了中间");
      }
    },
    bruk() {
      this.$router.go(-1);
    },
    onSelect(action) {
      // if (action.text == "首页") {
      //   this.$router.push("/");
      // }
      switch (action.text) {
        case "首页":
          this.$router.push("/");
          break;

        case "帮助":
          this.showHelomark = true;
          break;

        default:
          break;
      }
    },
    // 底部菜单按钮相关
    onsetting() {
      this.showbtm = true;
      this.ismenu = !this.ismenu;
    },
    onhuazhi() {
      this.showHuazhi = true;
      this.ismenu = !this.ismenu;
    },
    onliangdu() {
      this.showLiangdu = true;
      this.ismenu = !this.ismenu;
    },
    ondrag(val) {
      if (val >= 2) {
        this.$refs.page.scrollTop = this.allhei[this.lookindex][val - 2];
      } else {
        if (this.lookindex == 0) {
          this.$refs.page.scrollTop = 0;
        } else {
          this.$refs.page.scrollTop = this.allhei[this.lookindex - 1][
            this.allhei[this.lookindex - 1].length - 1
          ];
        }
      }
    },
    // 亮度调节按钮
    onbtnser(num, val) {
      this.LDact = num;
      this.LDvalue = val;
    },
    onstartTD() {
      this.LDact = 0;
    },
    // 漫画上下一章相关 ---------------------------------------
    toNext() {
      if (this.seeindex > 0) {
        this.seeindex--;
        this.seelist.push(this.cartoonlist[this.seeindex]);
        // 还需要一个判断。 要加载的下一张是否已存在
        if (this.seelist.length > 3) {
          this.$refs.page.scrollTop = 0;
          this.seelist.shift();
          window.setTimeout(() => {
            this.$refs.page.scrollTop =
              this.seelistHei[1] - this.$refs.page.offsetHeight;
          }, 80);
        }
      } else if (this.seeindex == 0) this.isnoimg = true;
    },
    previousPage() {
      let idx = this.seelist.findIndex((item) => {
        return this.cartoonlist[this.lookin].chapter_name == item.chapter_name;
      });

      if (idx) {
        if (idx == 1) {
          this.$refs.page.scrollTop = 1;
          this.$refs.page.scrollTop = 0;
        } else if (idx == 2) {
          this.$refs.page.scrollTop = this.seelistHei[idx - 2];
        }
        // this.lookin++;
      } else {
        if (this.lookin + 1 < this.cartoonlist.length)
          this.seelist.unshift(this.cartoonlist[this.lookin + 1]);

        this.$refs.page.scrollTop = 0;
        if (this.seelist.length > 3) {
          this.seelist.pop();
          this.seeindex++;
        }
        this.lookin++;
      }
      // if (this.seelist.length == 1) {
      //   this.seelist.unshift(this.cartoonlist[this.lookin + 1]);
      //   this.lookin++;
      // }else{
      //   let idx = this.seelist.findIndex((item) => {
      //     return (
      //       this.cartoonlist[this.lookin].chapter_name == item.chapter_name
      //     );
      //   });
      //   console.log(idx);
      // }
    },
    nextPage() {
      if (this.seelist.length == 1) {
        this.$refs.page.scrollTop = this.$refs.imglist.children[0].offsetHeight;
        window.setTimeout(() => {
          this.$refs.page.scrollTop = this.$refs.imglist.children[0].offsetHeight;
        }, 100);
        this.toNext();
      } else {
        let idx = this.seelist.findIndex((item) => {
          return (
            this.cartoonlist[this.lookin].chapter_name == item.chapter_name
          );
        });

        if (idx >= this.seelist.length - 1) {
          this.toNext();

          idx = this.seelist.findIndex((item) => {
            return (
              this.cartoonlist[this.lookin].chapter_name == item.chapter_name
            );
          });
        }
        // console.log(idx);
        if (idx == 2) return;

        let alltop = 0;

        window.setTimeout(() => {
          this.$refs.imglist.children.forEach((item, index) => {
            if (idx >= index) {
              alltop += item.offsetHeight;
            }
          });
          this.$refs.page.scrollTop = alltop;
        }, 200);
      }
    },
    // 右侧目录相关 --------------------------------------------
    onmulu() {
      this.showrig = true;
      this.ismenu = !this.ismenu;

      let num = this.iszf
        ? this.lookin
        : this.cartoonlist.length - 1 - this.lookin;

      window.setTimeout(() => {
        this.$refs.mululist.parentNode.scrollTop =
          num * this.$refs.mululist.children[0].offsetHeight;
      }, 100);
    },
    onbc(item, idx) {
      this.showrig = !this.showrig;

      idx = this.iszf ? idx : this.fancartoonlist.length - idx - 1;
      this.$refs.page.scrollTop = 0;

      this.value = 1;
      this.lookin = idx;
      this.seeindex = idx;
      this.seelist = [item];
    },
    onshuqian(el) {
      let idx = this.cartoonlist.findIndex((item) => {
        return el.chapter_id == item.chapter_id;
      });

      this.onbc(el, idx);
    },
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    getBookData() {
      this.showrig = !this.showrig;
      this.$store.commit("setBlackHome", false);

      var id = this.setuid(this.bookitem.comic_id);
      this.$router.push({ path: "/book", query: { id } });
      //   this.$store.dispatch("getBookItemData",this.bkitem.comic_id);
    },
    addbookmark() {
      this.showrig = !this.showrig;
      this.$store.commit("setBookMark", this.cartoonlist[this.lookin]);
      this.$store.commit("HcCartoon");
    },
    deleteAll() {
      this.$store.commit("removeBookMark");
    },
    // 漫画，计步器
    jsImg() {
      let arr = [[], [], []];
      let hei = 0;
      this.$refs.imglist.children.forEach((el, idx) => {
        if (idx <= 2) {
          el.children.forEach((item, num) => {
            hei += item.offsetHeight;
            arr[idx][num] = hei;
          });
        }
      });
      return arr;
    },
    // 自动播放
    autoLook() {
      this.$refs.page.scrollTop = this.$refs.page.scrollTop += 1;

      if (
        this.isnoimg &&
        this.$refs.page.scrollTop ==
          this.$refs.page.scrollHeight - this.$refs.page.offsetHeight
      ) {
        window.clearInterval(this.autotimer);
        this.isStart = false;
      }
    },
    onautoplay() {
      this.isStart = !this.isStart;

      if (this.autotimer) window.clearInterval(this.autotimer);
      if (this.isStart)
        this.autotimer = window.setInterval(this.autoLook, 51 - this.speed);
    },
    onspeed() {
      window.clearInterval(this.autotimer);
      this.autotimer = window.setInterval(this.autoLook, 51 - this.speed);
    },
  },
  components: {
    OneAChapter,
    bookmarkCatalog,
  },
  mounted() {
    window.setTimeout(() => {
      this.allhei = this.jsImg();
    }, 250);
    this.$refs.page.addEventListener("scroll", (e) => {
      var maxHei = e.target.scrollHeight - e.target.offsetHeight;

      // 判断当前是第几章节
      this.seelistHei = [];
      var leng = this.isnoimg
        ? this.$refs.imglist.children.length - 1
        : this.$refs.imglist.children.length;

      this.$refs.imglist.children.forEach((item, index) => {
        if (index < leng) {
          this.seelistHei.push(
            index
              ? item.offsetHeight + this.seelistHei[index - 1]
              : item.offsetHeight
          );
        }

        if (
          e.target.scrollTop >=
            (index ? this.seelistHei[index - 1] - 300 : 0) &&
          e.target.scrollTop <= this.seelistHei[index] - 300
        ) {
          this.lookin = this.seeindex - index + leng - 1;
        }
      });

      this.lookindex = this.seelist.findIndex((el) => {
        return this.cartoonlist[this.lookin].chapter_id == el.chapter_id;
      });
      // 判断当前是第几张图片
      this.allhei = this.jsImg();
      this.value =
        this.allhei[this.lookindex].findIndex((item) => {
          return e.target.scrollTop + 400 < item;
        }) + 1;

      // let lookheight;
      // if(lookindex){
      //   lookheight = this.seelistHei[lookindex] - this.seelistHei[lookindex-1];
      // }else{
      //   lookheight = this.seelistHei[lookindex];
      // }

      // 触顶时
      // if (e.target.scrollTop == 0) {
      //   if (this.seeindex < this.cartoonlist.length - 1) {
      //     // 弹出loading效果。等加载好了。在显示上一章。第一页
      //     this.seeindex++;
      //     this.seelist.unshift(this.cartoonlist[this.seeindex]);

      //     if (this.seelist.length > 3) {
      //       this.seelist.pop();
      //     }

      //     // 隐藏底部
      //     this.isnoimg = false;
      //   } else {
      //     console.log("已经是第一章了");
      //   }
      // }

      // 触底时
      if (e.target.scrollTop == maxHei) {
        console.log("到底了");
        this.toNext();
      }
    });

    // 点击松开后触发的事件
    this.$refs.page.addEventListener("touchstart", (e) => {
      this.touchstartX = e.changedTouches[0].clientX;
      this.touchstartY = e.changedTouches[0].clientY;
      this.touchstarttime = new Date().getTime();
    });
    this.$refs.page.addEventListener("touchend", (e) => {
      if (e.target.localName != "img") return;

      let touchendtime = new Date().getTime();
      if (
        touchendtime - this.touchstarttime < 200 &&
        Math.abs(e.changedTouches[0].clientX - this.touchstartX) < 20 &&
        Math.abs(e.changedTouches[0].clientY - this.touchstartY) < 20
      ) {
        this.jsScreen(e.changedTouches[0].clientX, e.changedTouches[0].clientY);
      }
      // console.log(e);
    });
  },
  created: function () {
    // console.log("created",this.$route.params.idx);
    this.value = 1;
    this.lookin = this.$route.params.idx;
    this.seeindex = this.$route.params.idx;
    this.seelist = [this.$route.params.itm];

    let cartoon = window.localStorage.getItem("cartoonList");
    if(!cartoon) this.showHelomark = true;
  },
  activated: function () {
    // console.log("activated",this.$route.params.idx);
    this.value = 1;
    this.lookin = this.$route.params.idx;
    this.seeindex = this.$route.params.idx;
    this.seelist = [this.$route.params.itm];
    this.allhei = this.jsImg();
  },
  beforeRouteUpdate: function () {
    // console.log("beforeRouteUpdate",this.$route.params.idx);
    this.value = 1;
    this.lookin = this.$route.params.idx;
    this.seeindex = this.$route.params.idx;
    this.seelist = [this.$route.params.itm];
    this.allhei = this.jsImg();
  },
  beforeRouteLeave(to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    to, from;
    this.value = 1;
    this.lookin = null;
    this.seeindex = null;
    this.isnoimg = false;

    this.isStart = false;
    window.clearInterval(this.autotimer);
    next();
  },
};
</script>

