<template>
  <div class="authordata">
    <img :src="uid" :onerror="urler" />
    <div class="rig-author">
      <h5>
        {{ author.Uname }}
        <!-- <van-tag round type="primary" color="#fc6858">new</van-tag> -->
      </h5>
      <p>
        粉丝<span>{{ author.Cfans }}</span
        >人
      </p>
    </div>
    <div class="allbtn">
      <van-button v-if="showbtn" @click="showbtn = !showbtn" icon="like-o" color="#999" plain hairline type="primary"
        >粉Ta</van-button
      >
      <van-button v-else @click="showbtn = !showbtn" icon="like" color="#fc6858" type="info">
        已粉
      </van-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["author"],
  data(){
    return {
      showbtn: true,
    }
  },
  computed: {
    uid() {
      return (
        "https://image.zymkcdn.com/file/head/" +
        this.setuid(this.author.Uid) +
        ".jpg"
      );
    },
    urler() {
      return "this.src='https://m.zymk.cn/static/images/default/error.png'";
    },
  },
  methods: {
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
  },
};
</script>

<style lang="less">
.authordata {
  position: relative;
  display: flex;
  align-items: center;

  h5,
  p {
    margin: 0;
    font-size: 12px;
    font-weight: 500;
  }

  img {
    width: 10vw;
    border-radius: 50%;
    margin-right: 8px;
  }

  .rig-author {
    h5 {
      font-size: 15px;
    }
    p span {
      color: #fc6858;
      line-height: 1.5;
    }
  }

  .allbtn{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);

    .van-button{
      height: 24px;
      padding: 0 6px;
    }
  }
}
</style>