<template>
  <div class="search">
    <div class="search-nav">
      <div class="bruk">
        <van-icon @click="bruk" name="revoke" size="22" color="#fc695a" />
      </div>
      <input
        v-model="inptext"
        @input="onjianyi"
        @keyup.enter="goResult"
        type="text"
        placeholder="可按 漫画名 或 漫画家 搜索"
      />
      <div class="search-goin">
        <van-icon name="search" size="22" color="#fc695a" @click="goResult" />
      </div>
    </div>

    <SearchHome
      @updata:addoldlist="addolddata($event)"
      @updata:deloldlist="delolddata($event)"
      v-if="showhome"
      :old="olddata"
    />
    <SearchJianYi
      @updata:addoldlist="addolddata($event.comic_name)"
      v-else
      :booklist="jianyi"
    />
  </div>
</template>

<script>
import SearchHome from "@/components/SearchHome.vue";
import SearchJianYi from "@/components/SearchJianYi.vue";

export default {
  data() {
    return {
      inptext: "",
      timer: null,
      showhome: true,
      jianyi: [],
      olddata: [],
    };
  },
  methods: {
    onjianyi() {
      if (this.inptext.trim() != "") {
        this.showhome = false;
      } else {
        this.showhome = true;
        window.clearTimeout();
        return;
      }

      if (this.timer != null) window.clearTimeout();
      this.timer = window.setTimeout(() => {
        this.axios
          .get(
            "http://api.zymk.cn/app_api/v5/getsortlist_new/?key=" + this.inptext
          )
          .then((response) => {
            this.jianyi = response.data.data.page;
          });
      }, 100);
    },
    goResult() {
      if (this.inptext == "") return;

      if (this.olddata.length != 0) {
        this.olddata = this.olddata.filter((item) => {
          return this.inptext != item;
        });
      }
      this.olddata.unshift(this.inptext);
      this.upHistory();

      this.$router.push({ path: "/result", query: { name: this.inptext } });
    },
    addolddata(str) {
      if (this.olddata.length != 0) {
        this.olddata = this.olddata.filter((item) => {
          return str != item;
        });
      }
      this.olddata.unshift(str);
      // 更新缓存
      this.upHistory();
    },
    delolddata(str) {
      this.olddata = this.olddata.filter((item) => {
        return str != item;
      });
      // 更新缓存
      this.upHistory();
    },
    bruk() {
      this.$router.go(-1);
      this.inptext = "";
      this.showhome = true;
    },
    upHistory() {
      let str = JSON.stringify(this.olddata);
      window.localStorage.setItem("cartoonHistory", str);
    },
  },
  created() {
    if (window.localStorage.getItem("cartoonHistory")) {
      this.olddata = JSON.parse(window.localStorage.getItem("cartoonHistory"));
    }
  },
  watch: {
    $route(to) {
      if (to.path=="/search" && window.localStorage.getItem("cartoonHistory")) {
        this.olddata = JSON.parse(
          window.localStorage.getItem("cartoonHistory")
        );
      }
    },
  },
  components: {
    SearchHome,
    SearchJianYi,
  },
};
</script>

<style lang="less">
.search {
  padding-top: 62px;
  .search-nav {
    position: fixed;
    top: 0;
    z-index: 999;

    width: 100vw;
    display: flex;
    align-items: center;
    padding: 16px 0;
    background: white;

    .bruk {
      i {
        padding: 0 10px;
      }
    }
    input {
      box-sizing: border-box;
      flex-grow: 1;
      outline: none;
      padding: 0 10px;
      margin: 0 5px;

      font-size: 15px;
      line-height: 32px;
      border: 1px solid rgba(255, 145, 117, 0.6);
      border-radius: 5px;
    }
    .search-goin {
      i {
        padding: 0 10px;
      }
    }
  }
}
</style>