<template>
  <li @click="onbc" :class="['van-hairline--bottom', { acts: showacts }]">
    <div
      class="li-bg"
      :style="{
        backgroundImage: imgurl,
        backgroundRepeat: 'no-repeat',
        backgroundSize: '110% auto',
        backgroundPosition: 'center',
      }"
    ></div>
    <p class="van-ellipsis">
      {{ itemdata.chapter_name + " " }}
      <span>{{ isshuqian ? "" : itemdata.chapter_title }}</span>
    </p>
    <div v-if="isshuqian" class="deletebox" @click.stop="delbookmark">
      <van-icon name="clear" color="#fc6454" size="14" />
    </div>
  </li>
</template>

<script>
export default {
  props: ["itemdata", "lookinbook", "isshuqian"],
  computed: {
    imgurl() {
      return (
        "url(https://mhpic.xiaomingtaiji.net/comic/" +
        this.itemdata.chapter_image.low.replace("$$", 1) +
        ")"
      );
    },
    showacts() {
      return this.jsActs();
    },
  },
  methods: {
    onbc() {
      this.$emit("update:bookcatalog");
    },
    jsActs() {
      if (!this.itemdata || !this.lookinbook) return false;
      return this.lookinbook.chapter_id == this.itemdata.chapter_id;
    },
    delbookmark() {
      this.$store.commit("delBookmark",this.itemdata);
      this.$store.commit("HcCartoon");
    },
  },
};
</script>

<style scoped lang="less">
li {
  position: relative;
  padding: 4%;
  list-style: none;
  .li-bg {
    margin-bottom: 2px;
    height: 15vw;
    max-height: 100px;
  }

  p {
    margin: 0;
    color: white;
    font-size: 12px;
    line-height: 1.4;
  }

  &.acts {
    background-color: #fc6454;
  }
  .deletebox {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 9;

    width: 14px;
    height: 14px;
    background: white;
    border-radius: 50%;
    font-size: 0;
    transform: translate(0, -10%);
  }
}
</style>