<template>
  <div class="bookpage">
    <i v-if="showbg"></i>
    <!-- http://getcomicinfo-globalapi.zymk.cn/app_api/v5/getcomicinfo/?comic_id= -->
    <van-nav-bar
      @click-left="bruk"
      :fixed="true"
      :border="false"
      :z-index="9999"
    >
      <template #left>
        <van-icon
          name="revoke"
          :color="showbg ? '#fff' : '#fc6858'"
          size="20"
        />
      </template>
      <template v-if="!showbg" #title>
        {{ bookitem.comic_name }}
      </template>
      <template #right>
        <van-icon
          v-if="!bookitem.dingyue"
          @click="dingyue"
          name="like-o"
          :color="showbg ? '#fff' : '#fc6858'"
          size="21"
        />
        <van-icon v-else @click="dingyue" name="like" color="#fc6858" size="21" />
        <van-icon
          name="comment-o"
          :color="showbg ? '#fff' : '#fc6858'"
          size="21"
        />
      </template>
    </van-nav-bar>

    <!-- {{$route.query.id}} -->

    <header class="book-top">
      <div
        class="bg-img"
        :style="{
          backgroundImage:
            'url(https://image.zymkcdn.com/file/cover/' +
            $route.query.id +
            '_3_4.jpg-300x400.jpg.webp)',
          backgroundRepeat: 'no-repeat',
          backgroundSize: '100% auto',
          backgroundPosition: 'center',
        }"
      >
        <i class="snak"></i>
      </div>
      <div class="book-box">
        <div class="book-cover">
          <img
            :src="
              'https://image.zymkcdn.com/file/cover/' +
              ($route.query.id || '000/000/001') +
              '_3_4.jpg-300x400.jpg.webp'
            "
          />
          <div class="score-box">
            <van-icon name="star" color="white" size="13" />
            <span> {{ bookitem.score / 10 }}</span>
          </div>
        </div>
        <div class="book-data">
          <h3>{{ bookitem.comic_name }}</h3>
          <p>{{ bookitem.author_name }}</p>
          <ul>
            <li v-for="item in bookitem.comic_type" :key="item.id">
              {{ item.name }}
            </li>
          </ul>
          <p>
            <van-icon name="fire-o" />
            {{ cpdNum(bookitem.total_click) }}
          </p>
          <div class="btns">
            <van-button
              @click="showPopup"
              color="orange"
              round
              plain
              hairline
              type="primary"
              >离线缓存</van-button
            >
            <van-button @click="golookin" color="#fc6858" round>{{
              bookitem.lookin != null
                ? "续看" + bookitem.chapter_list[bookitem.lookin].chapter_name
                : "开始阅读"
            }}</van-button>
            <van-popup
              v-model="show"
              closeable
              close-icon="close"
              position="bottom"
              :style="{ height: '30%' }"
            />
          </div>
        </div>
      </div>
    </header>

    <main class="book-cont">
      <van-tabs
        v-model="active"
        color="#fc6858"
        title-active-color="#fc6858"
        :border="true"
        animated
      >
        <van-tab title="详情">
          <div class="zuozhe">
            <!-- 剧情简介 -->
            <div class="synopsis">
              <h5>剧情简介</h5>
              <p>{{ this.bookitem.desc }}</p>
            </div>
            <!-- 作者信息 -->
            <div class="synopsis">
              <h5>作者信息</h5>
              <AuthorData :author="bookitem['author_info']" />
            </div>
            <!-- 作者公告 -->
            <div class="synopsis">
              <h5>作者公告</h5>
              <p>
                {{
                  bookitem["author_info"]
                    ? bookitem.author_info["notice"]
                    : "啥也没有呢"
                }}
              </p>
            </div>
            <SideslipList
              :alllist="
                bookitem.author_info ? bookitem.author_info.data : false
              "
              :showgd="true"
              >作者作品</SideslipList
            >
          </div>

          <SideslipList :alllist="bookitem.relation_list"
            >相关推荐</SideslipList
          >
        </van-tab>

        <van-tab>
          <template #title>
            目录
            <van-icon v-if="true" name="arrow-up" />
            <van-icon v-else name="arrow-down" />
          </template>

          <div class="catalog">
            <ul>
              <CatalogItem
                v-for="(item, index) in bookitem.chapter_list"
                :key="item.chapter_id"
                :catalog="item"
                :catalogindex="index"
              />
            </ul>
          </div>
        </van-tab>

        <van-tab title="支持"> 内容 支持 </van-tab>
      </van-tabs>
    </main>
  </div>
</template>

<script>
// import LabelType from '@/components/LabelType.vue'
import CatalogItem from "@/components/CatalogItem.vue";
import AuthorData from "@/components/AuthorData.vue";
import SideslipList from "@/components/SideslipList.vue";
import { mapState } from "vuex";

export default {
  data() {
    return {
      show: false,
      active: 1,
    };
  },
  computed: {
    ...mapState(["bookitem", "isblackhome", "showbg"]),
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    bruk() {
      this.isblackhome ? this.$router.go(-1) : this.$router.push("/");
      this.$store.commit("setBlackHome", true);
      // this.$store.dispatch("getBookItemData",this.bookitem.comic_id);
    },
    cpdNum(ind) {
      var k = 10000,
        sizes = ["", "万"],
        i;
      if (ind < k) {
        return ind;
      } else if (ind > 100000000) {
        ind = (ind / 100000000).toFixed(1) + "亿";
      } else {
        i = Math.floor(Math.log(ind) / Math.log(k));

        ind = (ind / Math.pow(k, i)).toFixed(0) + sizes[i];
      }

      return ind;
    },
    golookin() {
      if (this.bookitem.lookin != null) {
        this.$router.push({
          name: "lookcartoon",
          params: {
            idx: this.bookitem.lookin,
            itm: this.bookitem.chapter_list[this.bookitem.lookin],
          },
        });
      } else {
        let idx = this.bookitem.chapter_list.length - 1;
        this.$router.push({
          name: "lookcartoon",
          params: { idx, itm: this.bookitem.chapter_list[idx] },
        });
      }
    },
    dingyue(){
      this.$store.commit("setBookDingyue");
      this.$store.commit("HcCartoon");
    },
    luyou() {},
    onScr() {},
  },
  // watch: {
  //   $route(to){
  //     if(to.path == "/book"){
  //       var id = Number( to.query.id.replace(/\//g,"") );
  //       this.$store.dispatch("getBookItemData",id);
  //     }
  //   }
  // },
  components: {
    CatalogItem,
    AuthorData,
    SideslipList,
  },
  created() {
    var id = Number(this.$route.query.id.replace(/\//g, ""));
    this.$store.dispatch("getBookItemData", id);
  },
  mounted() {
    // window.addEventListener("scroll", () => {
    //   var scrollTop =
    //     window.pageYOffset ||
    //     document.documentElement.scrollTop ||
    //     document.body.scrollTop;
    //   if(scrollTop > 200){
    //     this.showbg = false;
    //   }else{
    //     this.showbg = true;
    //   }
    // });
  },
  beforeRouteUpdate(to, from, next) {
    var id = Number(to.query.id.replace(/\//g, ""));
    this.$store.dispatch("getBookItemData", id);
    next();
  },
};
</script>

<style lang="less">
.bookpage {
  > div:first-child.van-nav-bar {
    background-color: white;
  }

  .van-nav-bar {
    background: transparent;
  }

  header.book-top {
    position: relative;
    padding-bottom: 12px;

    .bg-img {
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;

      width: 100%;
      height: 45vw;

      .snak {
        display: block;
        height: 100%;
        background: rgba(0, 0, 0, 0.32);
      }
    }

    .book-box {
      display: flex;
      padding: 14vw 12px 0 12px;

      .book-cover {
        position: relative;
        border: 2px solid white;
        img {
          vertical-align: middle;
          width: 32vw;
        }

        .score-box {
          position: absolute;
          top: 0;
          right: 0;

          display: flex;
          align-items: center;
          padding: 2px 5px;
          background: rgba(0, 0, 0, 0.3);
          border-radius: 14px 0 0 14px;
          color: white;
          font-size: 13px;
          span {
            padding-left: 4px;
          }
        }
      }
      .book-data {
        flex-grow: 1;
        position: relative;

        flex-wrap: wrap;
        padding-left: 12px;
        color: white;

        h3 {
          margin: 0;
          padding-top: 2px;
        }
        p:nth-of-type(1) {
          margin: 0;
          font-size: 13px;
          line-height: 1.5;
        }
        ul {
          padding-top: 5%;
          height: 40px;
          font-size: 0;

          li {
            display: inline-block;
            padding: 0 4px;
            transform: scale(0.85);

            background: rgb(199, 133, 144);
            border-radius: 18px;
            font-size: 12px;
            line-height: 20px;

            &:nth-child(2) {
              background: rgb(243, 189, 126);
            }
            &:nth-child(3) {
              background: rgb(110, 196, 233);
            }
            &:nth-child(4) {
              background: rgb(102, 201, 187);
            }
            &:nth-child(5) {
              background: rgb(155, 155, 213);
            }
            &:nth-child(6) {
              background: rgb(219, 161, 210);
            }
          }
        }
        p:nth-of-type(2) {
          display: flex;
          align-items: center;
          margin: 0 0 8px 0;

          .van-icon {
            padding-right: 3px;
          }
        }
        .btns {
          position: absolute;
          bottom: 0;
          left: 12px;
          width: 100%;

          .van-button--normal {
            padding: 0 0;
          }
          .van-button {
            height: 32px;
            padding: 0 10px;

            &:first-child {
              margin-right: 4%;
            }
          }
        }
      }
    }
  }

  main.book-cont {
    .van-icon {
      transform: translateY(2px);
    }

    .zuozhe {
      border-bottom: 16px solid #faf9f9;
    }
    div.synopsis {
      border-bottom: 1px solid #d9d9d9;
      padding: 12px 0;

      h5 {
        color: #9c9c9c;
        font-size: 16px;
        font-weight: 500;
        margin: 12px 0;
        margin-top: 4px;
      }
      p {
        margin: 0;
        color: #6a6a6a;
        font-size: 14px;
        white-space: pre-wrap;
      }
    }

    .van-tabs__track {
      .van-tab__pane-wrapper {
        padding: 0 12px;
      }
    }

    .van-sticky--fixed {
      top: 46px;
    }
  }

  .van-nav-bar__title {
    color: rgb(252, 104, 88);
    font-size: 18px;
  }
  .van-nav-bar__right {
    i {
      margin-left: 14px;
    }
  }
}
</style>