<template>
  <div class="search-jianyi">
    <ul>
      <li
        v-for="item in booklist.comic_list"
        :key="item.comic_id"
        class="van-hairline--bottom"
        @click="goinbook(item)"
      >
        {{ item.comic_name }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["booklist"],
  methods: {
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    goinbook(obj) {
      console.log(obj);
      this.$emit("updata:addoldlist", obj);
      this.$store.dispatch("getBookItemData", obj.comic_id);
      this.$router.push({
        path: "/book",
        query: { id: this.setuid(obj.comic_id) },
      });
    },
  },
};
</script>

<style lang="less">
.search-jianyi {
  padding: 6px 0;

  li {
    line-height: 56px;
    text-indent: 46px;
  }
}
</style>