<template>
  <div class="ranking">
    <van-tabs
      v-model="active"
      animated
      title-active-color="#fc6858"
      :line-width="16"
      :border="true"
      sticky
    >
      <template #nav-left>
        <van-icon @click="$router.go(-1)" name="revoke" size="20" color="#fc6858" />
      </template>

      <van-tab :title="oneclass.title">
        <RankingListOne :tglclass="oneclass.order" />
      </van-tab> 
      <van-tab :title="twoclass.title">
        <RankingListTwo :tglclass="twoclass.order" :num="'one'" />
      </van-tab>
      <van-tab :title="threeclass.title">
        <RankingListTwo :tglclass="threeclass.order" :num="'two'" />
      </van-tab>
    </van-tabs>
    <div class="boter">没有更多了......</div>
  </div>
</template>

<script>
import RankingListOne from "@/components/RankingListOne.vue";
import RankingListTwo from "@/components/RankingListTwo.vue";

export default {
  data() {
    return {
      active: 0,
      oneclass: [],
      twoclass: [],
      threeclass: [],
    };
  },
  components: {
    RankingListOne,
    RankingListTwo,
  },
  created() {
    this.axios
      .get(
        "https://getconfig-globalapi.zymk.cn/app_api/v5/rankinglist/?refreshTime=1610005208&client-type=android&productname=zymk&platformname=android&client-channel=store_huawei&loglevel=3&client-version=5.9.0"
      )
      .then((response) => {
        this.oneclass = response.data.data[0];
        this.twoclass = response.data.data[1];
        this.threeclass = response.data.data[2];
      });
  },
};
</script>

<style lang="less">
.ranking {
  .van-tabs__nav {
    justify-content: center;

    .van-icon {
      position: absolute;
      left: 12px;
      top: 17%;
    }
    .van-tab {
      -webkit-box-flex: initial;
      -webkit-flex: initial;
      flex: none;
      margin: 0 7px;
      font-size: 17px;
    }
  }

  .boter{
    line-height: 40px;
    text-align: center;
  }
}
</style>