<template>
  <div class="searhome">
    <div class="hotsearch">
      <h2>热门搜索</h2>
      <ul class="hotlist">
        <li
          v-for="(item, index) in hot"
          :key="index"
          :style="{ backgroundColor: color[index] }"
          @click="goinbook(item)"
        >
          {{ item.comic_name }}
        </li>
      </ul>

      <ul class="oldlist">
        <li
          v-for="(item, index) in old"
          :key="index"
          class="van-hairline--bottom"
          @click="onold(item)"
        >
          <van-icon name="clock-o" color="#999" />
          <p>{{ item }}</p>
          <van-icon name="cross" color="#666" @click.stop="delold(item)" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["old"],
  data() {
    return {
      hot: [],
      color: [
        "#7ec933",
        "#ff7573",
        "#ffaa45",
        "#61bfff",
        "#00d0bd",
        "#a4abff",
        "#FFEADB",
        "#5B5656",
      ],
    };
  },
  methods: {
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    cpdNum(ind) {
      var k = 100000,
        sizes = ["", "万"],
        i;
      if (ind < k) {
        return ind;
      } else if (ind > 100000000) {
        ind = (ind / 100000000).toFixed(1) + "亿";
      } else {
        i = Math.floor(Math.log(ind) / Math.log(10000));

        ind = ind / Math.pow(10000, i);
        if (ind < 100) {
          ind = ind.toFixed(1) + sizes[i];
        } else {
          ind = ind.toFixed(0) + sizes[i];
        }
      }

      return ind;
    },
    goinbook(obj) {
      this.$emit("updata:addoldlist", obj.comic_name);
      this.$store.dispatch("getBookItemData", obj.comic_id);
      this.$router.push({
        path: "/book",
        query: { id: this.setuid(obj.comic_id) },
      });
    },
    ransercolor() {
      let differ = 8;
      let random = Math.random();
      return this.color[(differ * random).toFixed(0)];
    },
    onold(str) {
      this.$emit("updata:addoldlist", str);
      this.$router.push({ path: "/result", query: { name: str } });
    },
    delold(str) {
      this.$emit("updata:deloldlist", str);
    },
  },
  created() {
    this.axios
      .get("https://api.zymk.cn/app_api/v5/gethotsearch/")
      .then((response) => {
        this.hot = response.data.data.list;
      });
  },
};
</script>

<style lang="less">
.searhome {
  padding: 0 16px;
  h2 {
    color: #585858;
    font-size: 17px;
    font-weight: 400;
  }
  .hotlist {
    display: flex;
    flex-wrap: wrap;

    li {
      padding: 6px 8px;
      margin-right: 10px;
      margin-bottom: 10px;
      border-radius: 3px;
      color: white;
      background-color: tomato;
      font-size: 15px;
    }
  }
  .oldlist {
    li {
      display: flex;
      align-items: center;

      p {
        flex-grow: 1;
        box-sizing: border-box;
        padding: 0 10px;
      }
    }
  }
}
</style>