<template>
  <li class="sideslip-item" @click="getBookData">
    <div class="img-box">
      <img :src="id" />
      <div class="pinfeng">
        <van-icon name="star" />
        <span>{{ (bkitem.score / 10).toFixed(1) }}</span>
      </div>
      <div class="updateing">
        <p>{{ bkitem.last_chapter.name }}</p>
      </div>
    </div>
    <h4 class="shuming">{{ bkitem.comic_name }}</h4>
    <p class="miaoshu">{{ bkitem.comic_feature }}</p>
  </li>
</template>

<script>
export default {
  props: ["bkitem"],
  computed: {
    id() {
      return (
        "https://image.zymkcdn.com/file/cover/" +
        this.setuid(this.bkitem.comic_id) +
        "_3_4.jpg-300x400.jpg.webp"
      );
    },
  },
  methods: {
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    getBookData() {
      var id = this.setuid(this.bkitem.comic_id);

      this.$store.dispatch("getBookItemData", this.bkitem.comic_id);
      this.$router.push({ path: "/book", query: { id } });
      //   this.$store.dispatch("getBookItemData",this.bkitem.comic_id);
    },
  },
};
</script>

<style lang="less">
.sideslip-item {
  flex-shrink: 0;
  width: 25vw;
  margin-right: 12px;

  font-size: 12px;

  .img-box {
    position: relative;
    height: 31vw;
    margin-bottom: 4px;
    overflow: hidden;
    color: white;

    .pinfeng {
      position: absolute;
      right: 0;
      top: 0;

      background: rgba(0, 0, 0, 0.4);
      padding: 2px 4px;
      border-radius: 14px 0 0 14px;
    }
    .updateing {
      position: absolute;
      bottom: 0;

      width: 100%;
      background: rgba(0, 0, 0, 0.4);
      padding: 2px 0;

      p {
        text-align: right;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transform: scale(0.9);
      }
    }
    img {
      width: 100%;
      vertical-align: bottom;
    }
    span {
      color: white;
    }
  }
  .shuming {
    font-size: 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .miaoshu {
    color: rgb(172, 172, 172);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  h4,
  p {
    margin: 0;
    font-weight: 400;
  }
}
</style>