<template>
  <div class="rk-list-two">
    <van-tabs v-model="active" animated type="card">
      <van-tab v-for="(item, index) in tglclass" :key="index">
        <template #title>
          <!-- <van-icon name="more-o" /> -->
          <span
            class="imgs"
            :style="{
              backgroundImage:
                index == 0 ? 'url(' + NoOne + ')' : 'url(' + NoTwo + ')',
            }"
          ></span>
          {{ item.title }}
        </template>

        <div class="smallitem">
          <ul>
            <li
              v-for="(el, iis) in item.data"
              :key="iis"
              class="li-item-box"
            >
              <div class="imgbox">
                <img
                  :src="
                    'https://image.zymkcdn.com/file/head/' +
                    setuid(el.id) +
                    '.jpg'
                  "
                  :onerror="urler"
                />
              </div>
              <div class="bookcont">
                <h4>{{ el.name }}</h4>
                <p>
                  <template v-if="num=='one'">
                    共{{ el.comicnum }}部作品
                  </template>
                  <template v-else>
                    粉丝：{{ el.fans }}
                  </template>
                </p>
                <p>
                  <template v-if="num=='one'">
                    票数:<b>{{ cpdNum(el.orderby) }}</b>
                  </template>
                  <template v-if="index==0 && num=='two'">
                    元宝:<b>{{ cpdNum(el.orderby) }}</b>
                  </template>
                  <template v-if="index==1 && num=='two'">
                    活跃:<b>{{ cpdNum(el.orderby) }}</b>
                  </template>
                </p>
              </div>
              <div class="mingci">
                <i
                  v-if="iis == 0 || iis == 1 || iis == 2"
                  :class="{ one: iis == 0, two: iis == 1, three: iis == 2 }"
                ></i>
                <template v-else>{{ iis + 1 }}</template>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import NoOne from "@/assets/no12.png";
import NoTwo from "@/assets/no1.png";

export default {
  props: ["tglclass","num"],
  data() {
    return {
      active: 0,
      NoOne,
      NoTwo,
    };
  },
  computed: {
    urler() {
      return "this.src='https://m.zymk.cn/static/images/default/error.png'";
    },
  },
  methods: {
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    cpdNum(ind) {
      var k = 100000,
        sizes = ["", "万"],
        i;
      if (ind < k) {
        return ind;
      } else if (ind > 100000000) {
        ind = (ind / 100000000).toFixed(1) + "亿";
      } else {
        i = Math.floor(Math.log(ind) / Math.log(10000));

        ind = ind / Math.pow(10000, i);
        if (ind < 100) {
          ind = ind.toFixed(1) + sizes[i];
        } else {
          ind = ind.toFixed(0) + sizes[i];
        }
      }

      return ind;
    },
  },
};
</script>

<style lang="less">
.rk-list-two {
  p,h4{
    margin: 0;
    padding: 0;
  }
  .van-tabs__wrap {
    height: 110px;

    .van-tabs__nav--card {
      margin: 0;
      height: 110px;
      border: none;
      justify-content: initial;
      align-items: center;
      text-align: center;

      .van-tab.van-tab--active {
        color: rgb(0, 0, 0) !important;
        box-shadow: 0 3px 6px 1px rgb(221, 221, 221);
      }
      .van-tab {
        width: 110px;
        height: 80px;
        border: none;
        border-radius: 7px;
        font-size: 16px;

        &:nth-child(1) {
          color: #ffaf2f;
          background-color: #ffe7be;
        }
        &:nth-child(2) {
          color: #f04c6c;
          background-color: #ffcccc;
        }
      }
    }
  }

  .imgs {
    display: block;
    width: 40px;
    height: 40px;
    margin-left: 5px;
    background-size: 100%;
    background-repeat: no-repeat;
  }

  .smallitem {
    .li-item-box {
      display: flex;
      height: 29vw;
      padding: 12px;

      .imgbox {
        width: 29vw;
        min-width: 29vw;
        height: 29vw;
        border: 1px solid rgba(180, 180, 180, 0.7);
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 100%;
        }
      }
      .bookcont {
        box-sizing: border-box;
        flex-grow: 1;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;

        padding: 2.5vw;

        h4,
        p {
          width: 100%;
        }
        h4 {
          color: #666;
          font-size: 18px;
          font-weight: 400;
        }
        p:nth-child(2) {
          color: #999;
          font-size: 14px;
          transform: translateY(-2.5vw);

          span {
            padding-right: 4px;
          }
        }
        p:nth-child(3) {
          color: #999;

          b {
            color: #fc6858;
            font-weight: 400;
            padding-left: 6px;
          }
        }
      }
      .mingci {
        width: 10vw;
        min-width: 10vw;
        font-size: 24px;
        line-height: 29vw;
        text-align: center;

        i {
          display: inline-block;
          width: 30px;
          height: 30px;
          margin: 0 6px 0 0;
          background-size: 105% 105%;
          background-repeat: no-repeat;

          &.one {
            background-image: url("https://resource.mhxk.com/kanman_pc/static/images/comm/rank-top-1.png");
          }
          &.two {
            background-image: url("https://resource.mhxk.com/kanman_pc/static/images/comm/rank-top-2.png");
          }
          &.three {
            background-image: url("https://resource.mhxk.com/kanman_pc/static/images/comm/rank-top-3.png");
          }
        }
      }
    }
  }
}
</style>