<template>
  <div class="search-result">
    <div class="search-nav">
      <div class="bruk">
        <van-icon
          @click="$router.go(-1)"
          name="revoke"
          size="22"
          color="#fc695a"
        />
      </div>
      <!-- <p>{{ JSON.parse(this.$route.query.str)[0].text }}</p> -->
      <p>{{ this.$route.query["name"] ? this.$route.query.name : "..." }}</p>
      <div class="search-goin">
        <van-icon name="circle" size="22" color="#fc695a" />
      </div>
    </div>

    <ul v-if="booklist != undefined">
      <ItemBook
        v-for="(item, index) in booklist.comic_list"
        :key="index"
        :bkitem="item"
      />
    </ul>
  </div>
</template>

<script>
import ItemBook from "@/components/SideslipItem.vue";

export default {
  // computed: {
  //   booklist() {
  //     return (this.$route.query.str!=undefined && this.$route.path=="/result")
  //       ? JSON.parse(this.$route.query.str)[0].list
  //       : undefined;
  //   },
  // },
  data() {
    return {
      booklist: {},
    };
  },
  components: {
    ItemBook,
  },
  created() {
    this.axios
      .get(
        "http://api.zymk.cn/app_api/v5/getsortlist_new/?key=" +
          this.$route.query.name
      )
      .then((response) => {
        this.booklist = response.data.data.page;
      });
  },
  watch: {
    $route(to) {
      if (to.path == "/result") {
        this.axios
          .get(
            "http://api.zymk.cn/app_api/v5/getsortlist_new/?key=" +
              this.$route.query.name
          )
          .then((response) => {
            this.booklist = response.data.data.page;
          });
      }
    },
  },
};
</script>

<style lang="less">
.search-result {
  padding-top: 64px;

  .search-nav {
    position: fixed;
    top: 0;
    z-index: 999;

    width: 100vw;
    display: flex;
    align-items: center;
    padding: 16px 0;
    background: white;

    .bruk {
      i {
        padding: 0 10px;
      }
    }
    p {
      box-sizing: border-box;
      flex-grow: 1;
      margin: 0 5px;

      color: #fc7365;
      font-size: 18px;
      line-height: 32px;
      text-align: center;
    }
    .search-goin {
      i {
        padding: 0 10px;
      }
    }
  }

  > ul {
    display: flex;
    flex-wrap: wrap;

    li.sideslip-item {
      width: 30vw;
      margin: 0 1.6vw;

      .img-box {
        height: 40vw;
      }
    }
  }
}
</style>