<template>
  <li @click="LookChapter" class="catalog-item van-hairline--bottom">
    <div
      class="img-bg"
      v-lazy:background-image="imgurl"
      :style="{
        backgroundRepeat: 'no-repeat',
        backgroundSize: '125% auto',
        backgroundPosition: 'center',
      }"
    />
    <div class="right-cont">
      <h5 :class="{ lookin: lookin == catalogindex }">
        {{ catalog.chapter_name + "  " + catalog.chapter_title }}
      </h5>
      <p>
        {{ updateTime }}
        <van-tag
          v-if="this.days && lookin != catalogindex"
          round
          type="primary"
          color="#fc6858"
          >new</van-tag
        >
        <van-icon
          v-if="lookin == catalogindex"
          name="eye"
          size="22"
          color="#ff8275ba"
        />
      </p>
    </div>
  </li>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: ["catalog", "catalogindex"],
  computed: {
    imgurl() {
      return (
        "https://mhpic.xiaomingtaiji.net/comic/" +
        this.catalogimg[0] +
        "1" +
        this.catalogimg[1]
      );
    },
    catalogimg() {
      return this.catalog.chapter_image.high.split("$$");
    },
    updateTime() {
      return this.formatDate(this.catalog.create_time);
    },
    days() {
      return (
        (new Date().getTime() - this.catalog.create_time) /
          (1000 * 3600 * 24) <=
        15
      );
    },
    ...mapState({
      lookin: (state) => state.bookitem.lookin,
    }),
  },
  methods: {
    formatDate(now) {
      now = new Date(Number(now));

      var year = now.getFullYear(); //取得4位数的年份
      var month =
        now.getMonth() + 1 < 10
          ? "0" + (now.getMonth() + 1)
          : now.getMonth() + 1; //取得日期中的月份，其中0表示1月，11表示12月
      var date = now.getDate() < 10 ? "0" + now.getDate() : now.getDate(); //返回日期月份中的天数（1到31）

      return year + "-" + month + "-" + date;
    },
    LookChapter() {
      this.$router.push({
        name: "lookcartoon",
        params: { idx: this.catalogindex, itm: this.catalog },
      });
      // this.$store.commit("setseeindex",this.catalogindex);
      // this.$store.commit("setseelist",this.catalog);
      // this.$router.push({ name: "lookcartoon" });
      // this.$router.push({ name: "lookcartoon", query: {id: this.catalog.chapter_id} });
    },
  },
};
</script>

<style lang="less">
.catalog-item {
  list-style: none;
  display: flex;
  padding: 12px 0;

  div.img-bg {
    flex-shrink: 0;
    width: 36vw;
    height: 20vw;
    max-width: 250px;
    max-height: 131px;
    box-shadow: 0 0 6px 0 rgb(197, 194, 194);
  }

  .right-cont {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    padding: 2px 0;
    padding-left: 11px;

    h5.lookin {
      color: rgb(252, 104, 88);
    }
    p .van-icon {
      transform: translate(3px, 6px) !important;
    }
  }

  h5 {
    width: 100%;
    margin: 0;
    font-size: 15px;
    font-weight: 500;
  }
  p {
    margin: 0;
    font-size: 14px;
    color: #999;
  }
}
</style>