<template>
  <ul :style="{ opacity: val/10 }" class="one-a-chapter">
    <li v-for="index in onect.end_var" :key="index" class="imger">
      <img
        :src="
          'https://mhpic.xiaomingtaiji.net/comic/' +
          onect.chapter_addr +
          index +
          onect.image_suffix +
          definition[actHz]
        "
      />
    </li>
  </ul>
</template>

<script>
import { mapState } from 'vuex';
export default {
  props: ["onect","val"],
  data() {
    return {
      act: "-zymk.low.webp",
      definition: ["-zymk.low.webp", "-zymk.middle.webp", "-zymk.high.webp"],
    };
  },
  computed: {
    ...mapState(["actHz"]),
  }
};
</script>

<style lang="less">
.one-a-chapter {
  font-size: 0;
  background: #e9e7e7;

  .imger {
    img {
      width: 100%;
    }
  }
}
</style>