<template>
  <div class="rk-list-one">
    <van-tabs v-model="active" animated type="card">
      <van-tab v-for="(item, index) in tglclass" :key="index">
        <template #title>
          <!-- <van-icon name="more-o" /> -->
          <span
            class="imgs"
            :style="{ backgroundImage: 'url(' + urls[index] + ')' }"
          ></span>
          {{ item.title }}
        </template>

        <div class="smallitem">
          <ul>
            <li
              v-for="(el, iis) in item.data"
              :key="el.comic_id"
              class="li-item-box"
              @click="goinbook(el.comic_id)"
            >
              <div class="imgbox">
                <img
                  :src="
                    'https://image.zymkcdn.com/file/cover/' +
                    setuid(el.comic_id) +
                    '_3_4.jpg-300x400.jpg.webp'
                  "
                />
              </div>
              <div class="bookcont">
                <h4>{{ el.comic_name }}</h4>
                <p>
                  <span v-for="idxs in 3" :key="idxs">{{
                    el.comic_type[idxs - 1].name
                  }}</span>
                </p>
                <p>
                  人气:<b>{{ cpdNum(el.orderby) }}</b>
                </p>
              </div>
              <div class="mingci">
                <i
                  v-if="iis == 0 || iis == 1 || iis == 2"
                  :class="{ one: iis == 0, two: iis == 1, three: iis == 2 }"
                ></i>
                <template v-else>{{ iis + 1 }}</template>
              </div>
            </li>
          </ul>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  props: ["tglclass"],
  data() {
    return {
      active: 0,
      urls: [
        "https://cms.samanlehua.com/cms/book/027/200/409/25934.jpg-noresize.webp",
        "https://cms.samanlehua.com/cms/book/027/200/409/25935.jpg-noresize.webp",
        "https://cms.samanlehua.com/cms/book/027/200/409/25936.jpg-noresize.webp",
        "https://cms.samanlehua.com/cms/book/027/200/409/25937.jpg-noresize.webp",
        "https://f2.kkmh.com/image/210121/5KXbKVef3.png",
        "https://f2.kkmh.com/image/210121/wfzrn1g3k.png",
      ],
    };
  },
  methods: {
    setuid(item) {
      var id = (item / 1000).toFixed(3).split(".");
      var ids = (id[0] / 1000).toFixed(3).split(".");

      if (item < 1000) {
        item = "000/000/" + id[1];
      } else if (item < 1000000) {
        //   var arr = (item.id/1000).toFixed(3).split(".")[1]
        item = "000/" + ids[1] + "/" + id[1];
      } else {
        item =
          (ids[0] / 1000).toFixed(3).split(".")[1] + "/" + ids[1] + "/" + id[1];
      }

      return item;
    },
    cpdNum(ind) {
      var k = 100000,
        sizes = ["", "万"],
        i;
      if (ind < k) {
        return ind;
      } else if (ind > 100000000) {
        ind = (ind / 100000000).toFixed(1) + "亿";
      } else {
        i = Math.floor(Math.log(ind) / Math.log(10000));

        ind = ind / Math.pow(10000, i);
        if (ind < 100) {
          ind = ind.toFixed(1) + sizes[i];
        } else {
          ind = ind.toFixed(0) + sizes[i];
        }
      }

      return ind;
    },
    goinbook(id) {
      this.$store.dispatch("getBookItemData", id);
      this.$router.push({
        path: "/book",
        query: { id: this.setuid(id) },
      });
    },
  },
};
</script>

<style lang="less">
.rk-list-one {
  p,
  h4 {
    margin: 0;
    padding: 0;
  }

  .van-tabs__wrap {
    height: 110px;

    .van-tabs__nav--card {
      margin: 0;
      height: 110px;
      border: none;
      justify-content: initial;
      align-items: center;
      text-align: center;

      .van-tab.van-tab--active {
        color: rgb(0, 0, 0) !important;
        box-shadow: 0 3px 6px 1px rgb(221, 221, 221);
      }
      .van-tab {
        width: 75px;
        height: 85px;
        // padding-top: 36px;
        border: none;
        border-radius: 7px;
        font-size: 16px;

        &:nth-child(1) {
          color: #47dab2;
          background-color: #c1ffd5;
        }
        &:nth-child(2) {
          color: #a690ff;
          background-color: #dfd7ff;
        }
        &:nth-child(3) {
          color: #ffb236;
          background-color: #ffe7be;
        }
        &:nth-child(4) {
          color: #f04c6c;
          background-color: #ffcccc;
        }
        &:nth-child(5) {
          color: #ffaf2f;
          background-color: #ffe7be;
        }
        &:nth-child(6) {
          color: #49b7fb;
          background-color: #bfeaff;
        }
      }
    }
  }

  .imgs {
    display: inline-block;
    width: 40px;
    height: 40px;
    // background: url("https://cms.samanlehua.com/cms/book/027/200/409/25934.jpg-noresize.webp");
    background-size: 100%;
  }

  .smallitem {
    .li-item-box {
      display: flex;
      height: 29vw;
      padding: 12px;

      .imgbox {
        width: 36vw;
        min-width: 36vw;
        height: 29vw;
        overflow: hidden;
        img {
          width: 100%;
        }
      }
      .bookcont {
        box-sizing: border-box;
        flex-grow: 1;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;

        padding: 2.5vw;

        h4,
        p {
          width: 100%;
        }
        h4 {
          color: #333;
          font-size: 18px;
          font-weight: 400;
        }
        p:nth-child(2) {
          color: #999;
          font-size: 12px;
          transform: translateY(-2.5vw);

          span {
            padding-right: 4px;
          }
        }
        p:nth-child(3) {
          color: #999;

          b {
            color: #fc6858;
            font-weight: 400;
            padding-left: 6px;
          }
        }
      }
      .mingci {
        width: 10vw;
        min-width: 10vw;
        font-size: 24px;
        line-height: 29vw;
        text-align: center;

        i {
          display: inline-block;
          width: 30px;
          height: 30px;
          margin: 0 6px 0 0;
          background-size: 105% 105%;
          background-repeat: no-repeat;

          &.one {
            background-image: url("https://resource.mhxk.com/kanman_pc/static/images/comm/rank-top-1.png");
          }
          &.two {
            background-image: url("https://resource.mhxk.com/kanman_pc/static/images/comm/rank-top-2.png");
          }
          &.three {
            background-image: url("https://resource.mhxk.com/kanman_pc/static/images/comm/rank-top-3.png");
          }
        }
      }
    }
  }
}
</style>